
import {Box, Grid} from '@mui/material';
import ArrowCircleUpIcon from '@mui/icons-material/ArrowCircleUp';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import SubOutPrm from './SubOutPrm';
import * as UtilsCommon from '../utils/UtilsCommon';
import UtilsButton from '../utils/UtilsButton';

function CtrlClutch(props:UtilsCommon.DashboardContentProps) {

  const pram = {
    fontSize:"0.6rem",
    Button_h:"40px",
    Param_h:"82px",
    Param_w:"50px",
  };

  const ButtonSx = { 
    fontSize: pram.fontSize,
    fontFamily: 'Courier New, monospace',
    borderRadius: 0,
    height:pram.Button_h,
    backgroundColor:'gray',
    '&:hover': {
      backgroundColor: 'gray',
      filter: 'brightness(0.8)',
      border:'none',
    },
    color:'white',
    border:'none',
    boxShadow: '2px 2px 5px gray',
  }

  const ButtonAutoSx = { 
    fontSize: pram.fontSize,
    fontFamily: 'Courier New, monospace',
    borderRadius: 0,
    height:pram.Button_h,
    backgroundColor:'gray',
    '&:hover': {
      backgroundColor: 'gray',
      filter: 'brightness(0.8)',
      border:'none',
    },
    color:'white',
    border:'none',
    boxShadow: '2px 2px 5px gray',
  }

  const GridSx = {
    width:'220px',
    //border:'1px solid black'
  }
 
  return (
    <div>
      <Grid container spacing={0.3} sx={GridSx}>
      <Grid item xs={0}>
          <Box sx={{mb:0.2}}>
            <UtilsButton 
                variant="outlined" 
                sx={ButtonAutoSx}
                props={props} 
                ButtonType={UtilsCommon.enmButtonType.auto_clutch_dw_on}
            >
              AUTO<br/>ON
            </UtilsButton>
          </Box>
          <Box sx={{mb:0.2}}>
            <UtilsButton 
                variant="outlined" 
                sx={ButtonAutoSx}
                props={props} 
                ButtonType={UtilsCommon.enmButtonType.auto_clutch_dw_off}
            >
              AUTO<br/>OFF
            </UtilsButton>
          </Box>
        </Grid>
        <Grid item xs={0}>
          <Box sx={{mb:0.2}}>
            <UtilsButton 
              variant="outlined" 
              startIcon={<ArrowCircleUpIcon />} 
              sx={ButtonSx}
              props={props} 
              ButtonType={UtilsCommon.enmButtonType.clutch_up}
            >
              クラッチ
            </UtilsButton>
          </Box>
          <Box>
            <UtilsButton 
              variant="outlined" 
              startIcon={<ArrowCircleDownIcon />} 
              sx={ButtonSx}
              props={props} 
              ButtonType={UtilsCommon.enmButtonType.clutch_dw}
            >
              クラッチ
            </UtilsButton>
          </Box>
        </Grid>
        <Grid item sx={{height:pram.Param_h}}>
          <SubOutPrm sx={{ height: pram.Param_h, width:pram.Param_w}}/>
        </Grid>
      </Grid>
    </div>
  )
}

export default CtrlClutch
